@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,400;0,700;1,100;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,400;0,700;1,100;1,400;1,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
    padding: env(safe-area-inset);
}

body {
  margin: 0;
  font-family: 'Open Sans', 'Segoe UI', 'Roboto',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;

    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#Work {
    content: " ";
    visibility: none;
    display: block;
    margin-top: -80px;
    padding-top: 80px;   
}

.Work {
    background-color: white;
    color: #1340E1;
    font-family: 'Raleway';
    font-size: 22px;

    margin: 20px auto;
    padding: 20px;
    width: 100%;
}

.Work-container {
    display: flex;
    flex-direction: column;
    max-width: 1400px;
    margin: auto;
}

.Work-blocks {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.Work-header {
    font-size: 82px;
    font-weight: bold;
    margin-bottom: 30px;
}


@media screen and (max-width: 960px) {
    .Work-header {
        font-size: 62px;
    }
}

@media screen and (max-width: 768px) {
    .Work-blocks {
        grid-row-gap: 10px;
        row-gap: 10px;
    }

    .Work-header {
        font-size: 42px;
    }
}

/* WorkItem Styles */


.Work-block {
    display: flex;
    margin-bottom: 50px;
}


/* WorkCard Styles */

.Work-card {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 330px;

    margin: 30px;

    background: #1340E1;
    color: white;
    border-radius: 35px;

    cursor: pointer;
}


.Work-card img {
    width: 300px;
    height: 246px;

    align-self: center;
    justify-self: center;

    border-radius: 20px;

    margin-bottom: 40px;
    margin: 20px;

    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.Work-card h3 {
    font-weight: 600;
}

.Work-card h4 {
    font-style: italic;
    margin: 5px;
}

.Work-tags {
    display: flex;
    flex-direction: column;
    margin: 20px;

    align-items: center;
}

.Work-tags span {
    display: flex;
    align-content: center;
    justify-content: center;
    margin: 5px;

    font-size: 18px;
}

.Work-tags i {
    align-self: center;
}

.Work-tags h5 {
    margin: 5px;
}

/* WorkModal Styles */
.WorkModal {
    display: flex;
    flex-direction: column;
}

.WorkModalContent {
    display: flex;
    margin: 10px;
    margin-bottom: 30px;
}

.Work-subheader {
    font-size: 42px;
    margin-bottom: 5px;
}

.Work-text h3 {
    font-size: 25px;
    font-style: italic;

    margin-bottom: 30px;
}


.Work-text {
    color: #1340E1;
    display: flex;
    flex-direction: column;

    line-height: 21px;
}



.WorkModal img {
    height: 200px;
    width: auto;

    margin: 20px;

    border: 5px solid #1340E1;
    border-radius: 20px;
}

.WorkModalFooter {
    display: flex;
    justify-content: center;
}

.WorkModalLearn, .Work-closebtn {
    width: 200px;
    align-self: center;
    border: none;

    padding: 10px;
    margin: 10px;
    border-radius: 30px;

    background: #1340E1;
    color: white;

    cursor: pointer;

    text-align: center;
}

.Work-closebtn:focus, .WorkModalLearn:focus {
    outline: none;
    text-decoration: none;
    color: white;
}

.WorkModalLearn {
    background-color: green;
    text-decoration: none;
}

@media screen and (max-width: 960px) {
    .Work-block {
        flex-direction: column;
    }

    .Work-subheader {
        font-size: 25px;
    }

    .Work-block p {
        font-size: 18px;
    }

    .Work-card {
        margin: 20px;
    }

    .Work-subheader {
        font-size: 29px;
        margin-bottom: 5px;
    }
    
    .Work-text h3 {
        font-size: 20px;
    }

    .WorkModalFooter {
        flex-direction: column;
    }

}

@media screen and (max-width: 768px) {
    .Work-block img {
        max-width: 275px;
    }

    .WorkModalContent {
        flex-direction: column;
    }

    .WorkModalContent img {
        width: 200px;
        height: 150px;
        justify-self: center;
        align-self: center;
    }
}

@media screen and (max-width: 450px) {
    .Work-card {
        width: 230px;
        margin: 10px;
    }

    .Work-card img {
        width: 200px;
        height: 146px;
    
        align-self: center;
        justify-self: center;
    
        margin-bottom: 30px;
        margin: 10px;
    }

    .Work-card h3, .Work-card h4 {
        font-size: 18px;
    }

    .Work-tags span {
        font-size: 16px;
        justify-self: center;
        align-self: center;
    }

    .Work-subheader {
        font-size: 27px;
    }
    
    .Work-text h3 {
        font-size: 18px;
        margin-bottom: 20px;
    }
}




