/* NavBar at top of page */
.NavBar {
    background-color: white;
    color: #1340E1;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    font-family: 'Open Sans';

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    position: -webkit-sticky;

    position: sticky;
    top: 0;

    height: 80px;

    transition: all 0.5s ease;
}

.NavBar-nameLogo {
    font-family: 'Bebas Neue';
    font-size: 52px;

    margin-left: 10px;
}

.NavBar-menuicon {
    display: none;
}

.fa-bars {
    color: #1340E1;
}

.NavBar-list {
    display: flex;
    justify-content: space-between;
}

.NavBar-list a {
    font-size: 25px;
    font-weight: bold;
    color: #1340E1;

    list-style: none;
    
    margin: 32px;
}

.NavBar-list a:hover {
    text-decoration: none;
    color: #14C9E1;
}

.NavBar.offset .NavBar-list a:hover {
    text-decoration: none;
    color: #14C9E1;
}

.NavBar.offset .NavBar-list.active a:hover {
    text-decoration: none;
    color: white;
}

/* NavBar offset from top of page */
.NavBar.offset {
    background-color: #1340E1;
    color: white;

    transition: all 0.5s ease;
}

.NavBar.offset .fa-bars {
    color: white;
}

.NavBar.offset a {
    color: white;
}


@media screen and (max-width: 1060px) {

    .NavBar-menuicon {
        display: block;
        position: absolute;
        right: 20px;
        font-size: 1.8rem;
        cursor: pointer;
    }

    .NavBar-list {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        width: 100%;
        height: 100vh;
        opacity: 1;

        position: absolute;
        top: 80px;
        left: -100%;
        transition: all 0.5s ease;
    }
    
    .NavBar-list.active {
        position: absolute;
        top: 80px;
        left: 0;

        background: #FFF;
        transition: all 0.5s ease;
    }

    .NavBar-list a {
        color: #1340E1;
        font-style: 'Open Sans';
        font-size: 25px;

        margin-left: 10px;
        margin-top: 20px;
        margin-bottom: 20px;

        padding-bottom: 10px;

        border-bottom: 3px solid #1340E1;
    }

    .NavBar.offset .NavBar-list.active {
        background-color: #1340E1;
    }
    
    .NavBar.offset .NavBar-list.active a {
        color: white;
        border-bottom: 3px solid white;
    }

    .NavBar-nameLogo {
        font-size: 42px;
    }
}


